import React from 'react'
// @mui
import Grid from '@mui/material/Grid';
import { MonetizationOn, Person, Receipt, Tour } from '@mui/icons-material';

// third-party
import Chart from "react-apexcharts";

// project
import MainCard from 'components/cards/MainCard';
import { useConfig } from 'providers/Config';
import { OrderListResults } from 'pages/orders/sections/ordersListResults';

//assets
import './index.css';

// mocks
import { ORDERS } from '__mocks__/orders';
import { spark1, spark2, spark3, spark4, optionsBar, optionsArea, optionsCircle } from './chart-data';
import PageContainer from 'layouts/PageContainer';
import ChartCard from 'components/cards/widgets/ChartCard';
import useChart from '../../../hooks/useChart';

export default function Overview() {
  // const theme = useTheme();
  const { appConfig } = useConfig();
  const { initialGridSpacing: gridSpacing } = appConfig.theme;
  const spark1Mixed = useChart(spark1);
  const spark2Mixed = useChart(spark2);
  const spark3Mixed = useChart(spark3);
  const spark4Mixed = useChart(spark4);
  const optionsAreaMixed = useChart(optionsArea);
  const optionsCircleMixed = useChart(optionsCircle);
  const optionsBarMixed = useChart(optionsBar);
  return (
    <PageContainer>
      <Grid container spacing={gridSpacing}>
        <Grid item container xs={12} spacing={gridSpacing}>
          <Grid item xs={12} sm={6} md={3} >
            <ChartCard title="Orders" value={'2,882'} options={spark3Mixed} icon={<Receipt />} />
          </Grid>
          <Grid item xs={12} sm={6} md={3} >
            {/* <ChartCard title="Bounce Rate" value={'61.5%'} options={spark1} color="warning" icon={<MonetizationOn />} /> */}
            <ChartCard title="Sales" value={'$93,282'} options={spark1Mixed} color="warning" icon={<MonetizationOn />} />
          </Grid>
          <Grid item xs={12} sm={6} md={3} >
            <ChartCard title="Users" value={'10,292'} options={spark2Mixed} color="error" icon={<Person />} />
          </Grid>
          <Grid item xs={12} sm={6} md={3} >
            <ChartCard title="Visitors" value={'39,221'} options={spark4Mixed} color="info" icon={<Tour />} />
          </Grid>
        </Grid>
        <Grid item xs={12} md={8} >
          <MainCard title="Quarterly Sales">
            <Chart
              options={optionsAreaMixed}
              series={optionsArea.series}
              type="area"
              width="100%"
              height={340}
            />
          </MainCard>
        </Grid>
        <Grid item xs={12} md={4}   >
          <MainCard title="Monthly Country Sales">
            <Chart
              options={optionsCircleMixed}
              series={optionsCircle.series}
              type="radialBar"
              width="100%"
              height={380}
            />
          </MainCard>
        </Grid>
        <Grid item xs={12} md={8} >
          <MainCard title={`Latest Orders`} >
            <OrderListResults orders={ORDERS} />
          </MainCard>
        </Grid>
        <Grid item xs={12} md={4}   >
          <MainCard title='Quarterly Sales'>
            <Chart
              options={optionsBarMixed}
              series={optionsBar.series}
              type="bar"
              width="100%"
              height={395}
            />
          </MainCard>
        </Grid>
      </Grid>
    </PageContainer>
  );
}
