import { faker } from '@faker-js/faker';
import { sample } from 'lodash';

export const STATUS = ['Placed', 'Processed', 'Delivered', 'Complete']

export const ORDERS = [...Array(12)].map((_, index) => ({
  id: faker.datatype.uuid(),
  customerId: faker.datatype.uuid(),
  customerName: faker.name.fullName(),
  invoiceId: faker.datatype.uuid(),
  promotionCode: faker.datatype.number(),
  totalAmount: faker.datatype.number({ min: 4, max: 1999, precision: 0.01 }),//faker.datatype.number(),
  description: faker.commerce.productDescription(),
  name: faker.commerce.productName(),
  status: sample(STATUS),
  items: [{
    productId: faker.datatype.uuid(),
    name: faker.commerce.productName(),
    price: faker.datatype.number({ min: 4, max: 99, precision: 0.01 }),
    amount: faker.datatype.number(),
  },
  {
    productId: faker.datatype.uuid(),
    name: faker.commerce.productName(),
    price: faker.datatype.number({ min: 4, max: 99, precision: 0.01 }),
    amount: faker.datatype.number(),
  }
  ],
  createdAt: faker.date.past(),
}));

